@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Josefin Sans", sans-serif;font-size:24px;color:#fffc;background:linear-gradient(to left, #c94b4b, #4b134f)}.App{min-width:350px;width:700px;margin:50px auto;padding:8px;background:#9a25a926;box-shadow:0 8px 32px 0 #1f26875e;-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);border-radius:10px;border:1px solid #ffffff2e}.title{text-align:center;margin-bottom:16px}.good{display:flex;justify-content:space-between;margin-bottom:8px;align-items:center}.button{width:60px;height:36px;border-radius:4px;background-color:#ffffff1a;border:1px solid #ffffff1a;-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px);color:#fffc;font-size:24px;cursor:pointer}.button:hover{background-color:#fff3}

